.FlexGrid {
    /* width: 70vw; */
    display: grid;
    /* height: 100vh; */
    grid-template: repeat(auto-fill, minmax(180px, 1fr)) / repeat(auto-fill, minmax(200px, 1fr));
    grid-gap: 0.5rem;
}

#mainPage {
    display: flex;
    width: 90vw;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.headerButton {
    width: 60vw;
    display: grid;
    grid-template: repeat(auto-fit, minmax(10px, 1fr)) / repeat(auto-fit, minmax(105px, 1fr));
    margin: 1rem 0;
    grid-column-gap: 0.2rem;
}

.pokemonItems {
    width: 80vw;
}

.header {
    display: flex;
    justify-content: center;
    /* width: 70vw; */
}

.buttonMenu {
    color: #fff;
    font-family: 'Varela Round', sans-serif;
    font-size: 1.5rem;
    background: rgb(125, 129, 126);
    border: 2px solid black;
    text-align: center;
    border-radius: 1rem;
    padding: 0.2rem 0;
    /* margin: 1rem 0; */
}

.buttonMenu:hover {
    background: rgb(0, 0, 0);
    color: #fff;
    border: 2px solid rgb(125, 129, 126);
}

.header>span {
    text-align: center;
    color: #fff;
    font-family: 'Lobster', cursive;
    font-size: 4rem;
    padding: .7rem 0;
}

.loadMore {
    display: grid;
    color: #fff;
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
    background: rgb(125, 129, 126);
    border: 2px solid black;
    text-align: center;
    border-radius: 1rem;
    margin: 1rem 0;
}

.loadMore:hover {
    background: rgb(0, 0, 0);
    color: #fff;
    border: 2px solid rgb(125, 129, 126);
}

.popup {
    position: absolute;
    top: 1%;
    z-index: 6;
    transition: 1s;
    transform: translateY(-200%);
    color: #fff;
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
}

.hover_body {
    position: absolute;
    background: #a0a0a0;
    width: 100vw;
    /* height:100vh; */
    z-index: -1;
    opacity: 0;
}

.headers span {
    right: 10px;
}

.headers {
    position: relative;
    width: 100%;
    text-align: right;
}

.popup--active {
    position: absolute;
    top: 1%;
    z-index: 6;
    transition: 1s;
    color: #fff;
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
}

.button--active {
    opacity: 0.5;
}

.bodyFilter {
    padding: 1rem 0;
}

.filterContainer {
    display: grid;
    grid-template: repeat(auto-fill, minmax(30px, 1fr)) / repeat(auto-fill, minmax(200px, 1fr));
    gap: 0.1rem;
}

.filterButton {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.filterName {
    margin: 0 1rem;
    background: rgb(125, 129, 126);
    border: 2px solid black;
    border-radius: 1rem;
    padding: 0.2rem;
    width: 200px;
}

.selectFilter {
    background: #F2F2F2;
    border-radius: 50%;
    color: #313131;
    width: 28px;
    height: 28px;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid rgb(16, 166, 216);
}

.selectFilter:hover {
    color: #F2F2F2;
    background: #313131;
    border: 2px solid rgb(125, 129, 126);
}

.bodyPopup {
    box-sizing: border-box;
    width: 70vw;
    background: rgb(39, 39, 39);
    border: 2px solid rgb(16, 166, 216);
    text-align: center;
    border-radius: 1rem;
    padding: 0.2rem 1rem;
    margin: 1rem 0;
}

.setFilter {
    display: grid;
    grid-template-rows: 1fr;
    margin-bottom: 0.5rem;
}

.setFilter>* {
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
    color: #F2F2F2;
    border: 2px solid rgb(16, 166, 216);
    border-radius: 1rem;
    background: rgb(16, 166, 216);
}

.setFilter>*:hover {
    background: rgb(10, 95, 123);
    /* border: 2px solid rgb(10, 95, 123); */
    color: rgb(255, 255, 255);
    text-shadow: 1px 1px 0 rgb(0, 0, 0);
}

.selectFiter--Active {
    background: #F2F2F2;
    border-radius: 50%;
    color: #313131;
    width: 28px;
    height: 28px;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid rgb(255, 0, 0);
}