.pokemonPages{
    display: grid;
    margin: 1rem 0;
    grid-gap: 1rem;
    color: #fff;
    /* width: 80vw; */
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
    grid-template: minmax(180px, 1fr) / repeat(auto-fit, minmax(200px, 1fr));
    /* grid-template:  1fr / 1fr 1fr; */
    /* padding: 1rem 0 ; */
}
.pokemonImg{
    padding-top: 1rem ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-style: oblique;
    background: rgb(88, 92, 89);
    border: 2px solid black;
    border-radius: 1rem;
    text-shadow: -1px -1px #FFF,
               -2px -2px #FFF,
               -1px 1px #FFF,
               -2px 2px #FFF,
               1px 1px #FFF,
               2px 2px #FFF,
               1px -1px #FFF,
               2px -2px #FFF,
               -3px -3px 2px #BBB,
               -3px 3px 2px #BBB,
               3px 3px 2px #BBB,
               3px -3px 2px #BBB;
  color: rgb(88, 92, 89);
  transition: all 1s;
}
.pokemonImg:hover{
    color: rgb(255, 94, 0);
}
.pokemonImg > img {
    width: 300px;
}
.pokemonInformation{
    display: grid;
    grid-template: 1fr / auto;
    grid-gap: 0.3rem;
    padding: 1rem;
    background: rgb(125, 129, 126);
    border: 2px solid black;
    border-radius: 1rem;
   
}
.pokemonStats{
    border: 2px solid black;
    background: rgb(88, 92, 89);
    border-radius: 1rem;
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.452);
    transition: all 1s;
    text-align: center;
    padding: .2rem 0;
}
.pokemonStats:hover{
    background: rgb(255, 255, 255);
    color: rgb(88, 92, 89);
    text-shadow: 2px 2px 3px rgb(88, 84, 81);
    transition: all 1s;
}
.poketSlot{
    display: flex;
    flex-direction: column;
    background: #BBB;
    border-radius: 1rem;
    text-shadow: -1px -1px #FFF,
        -2px -2px #FFF,
        -1px 1px #FFF,
        -2px 2px #FFF,
        1px 1px #FFF,
        2px 2px #FFF,
        1px -1px #FFF,
        2px -2px #FFF,
        -3px -3px 2px #BBB,
        -3px 3px 2px #BBB,
        3px 3px 2px #BBB,
        3px -3px 2px #BBB;
    color: rgb(88, 92, 89);
    transition: all 1s;
    text-align: center;
    padding: .2rem 0;
}
.poketSlot:hover{
    color: rgb(255, 94, 0);
    background: rgb(57, 61, 58);
}