.Pokemon {
    display: flex;
    background: rgb(125, 129, 126);
    border: 2px solid black;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border-radius: 1rem;
    padding: 2em 0;
    font-family: 'Varela Round', sans-serif;
}
.Pokemon > img {
    height: 128px;
}

.Pokemon:hover {
    background: rgb(0, 0, 0);
    color: #fff;
    border: 2px solid rgb(125, 129, 126);
}

.Pokemon>span {
    font-size: 1.5em;
    color: rgb(255, 255, 255);
}

.gridBack {
    display: grid;
    grid-template: 1fr;
}

.buttonBack {
    color: #fff;
    font-family: 'Varela Round', sans-serif;
    font-size: 2rem;
    background: rgb(125, 129, 126);
    border: 2px solid black;
    text-align: center;
    border-radius: 1rem;
    margin: 1rem 0;
}

.buttonBack:hover {
    background: rgb(0, 0, 0);
    color: #fff;
    border: 2px solid rgb(125, 129, 126);
}

.poketInfo {
    display: grid;
    /* flex-direction: column; */
    width: 1fr
}